import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';

const CommunityBlock = styled.div`
    border-top:107px solid yellowgreen;
    padding:100px 0;
    .row{
        h2{
            text-align:center;
            font-size:30px;
        }
        ul{
            display:flex;
            flex-wrap:wrap;
            li{
                width:25%;
                margin:20px 0;
            }
        }
    }
`;

const Communitysection = () => {

    const [cleanair, setCleanair] = useState([])

    const getAir = async () => {
        const kim = await axios.get('/B552584/UlfptcaAlarmInqireSvc/getUlfptcaAlarmInfo?serviceKey=NfzrVnl%2FGtwWmzATZ%2FhDwRnupwhEm1SRTo4IiQg6HRvGRdJa1wF9gt4QFW9jhdBA%2B8q6NqX06Crk5ujvhcPIJw%3D%3D&returnType=json&numOfRows=100&pageNo=1&year=2020&itemCode=PM10')
        // console.log(kim)
        setCleanair(kim.data.response.body.items)
    }

    useEffect( ()=>{
        getAir()
    }, [cleanair] )

    return (
        <CommunityBlock>
            <div className="row">
                <h2>에어코리아 미세먼지 경보 발령 현황</h2>
                <ul>
                    { cleanair && cleanair.map( (data, index) => <li key={index}>{data.districtName}: {data.issueVal}</li> ) }
                </ul>
            </div>
        </CommunityBlock>
    );
};

export default Communitysection;